import React, {useEffect, useState} from "react";
import {Checkbox, List, Tag} from "antd";
import moment from "moment";
import {CloseOutlined} from "@ant-design/icons";

import {
    CheckboxContainer,
    DeleteButton,
    OpacityContainer,
    OpacityParagraph,
    WrapperTaskComponent,
} from "./task.styled";
import Url from "../../../../../../url/url";
import {editTask} from "./requests/editTask";
import {StatusesBlock} from "../../../../sales-funnel/helpers/sales-funnel-styled";
import {createComment} from "./requests/createComment";

export const TaskComponent = ({task, deleteTask, setComments, _id}) => {
    const [done, setDone] = useState(task.done);

    const resFIO = (key) => {
        if (!task.responsible || task.responsible.length === 0) return "Не найден";
        if (Array.isArray(task.responsible)) {
            return task.responsible[0][key];
        }
        if (
            !Array.isArray(task.responsible) &&
            task.responsible instanceof Object
        ) {
            return task.responsible[key];
        }
        return "...";
    };
    const onClickDeleteTask = async (id) => {
        await editTask(Url, id, {archived: true});
        deleteTask(id)
    };

    async function onChangeEditDone(id, ev) {
        const checked = ev.target.checked;

        await editTask(Url, id, {
            done: true,
            completedOn: moment().toISOString(),
            archived: true,
        });

        deleteTask(id)

        await createComment(Url, _id, {
            text: `${task.name}\n${task.text}`,
            color: "crimson",
        });
        setComments((prev) => [
            ...prev,
            {
                author: `${resFIO("surname")} ${resFIO("name")} ${resFIO("midname")}`,
                content: {props: {children: `${task.name}\n${task.text}`}},
                deadline: moment().get().toISOString(),
                color: "crimson",
            },
        ]);
    }

    useEffect(() => {
        return () => setDone(() => false)
    }, [])

    return (
        <List.Item key={task._id}>
            <WrapperTaskComponent done={done}>
                <div>
                    <OpacityContainer>
                        {task.name}
                        <StatusesBlock portable={false} contains={task.tags.length}>
                            {task.tags.map((tag) => {
                                return (
                                    <Tag key={tag._id} color={tag.color}>
                                        {tag.name}
                                    </Tag>
                                );
                            })}
                        </StatusesBlock>
                    </OpacityContainer>
                    <OpacityParagraph>
                        Срок до: {moment(task.deadline).format("DD/MM/YYYY | HH:mm")}
                    </OpacityParagraph>
                    <OpacityParagraph>
                        Ответственнен: {resFIO("surname")} {resFIO("name")}{" "}
                        {resFIO("midname")}
                    </OpacityParagraph>
                    <DeleteButton onClick={() => onClickDeleteTask(task._id)}>
                        <CloseOutlined/>
                    </DeleteButton>
                </div>
                <p className="done">{task.text}</p>
                <CheckboxContainer>
                    <Checkbox
                        checked={done}
                        onChange={(ev) => onChangeEditDone(task._id, ev)}
                    />
                </CheckboxContainer>
            </WrapperTaskComponent>
        </List.Item>
    );
};
