import {Comment, List} from "antd";
import moment from "moment";
import React from "react";

export const CommentList = ({comments}) => {
    return (
        <List
            dataSource={comments}
            header={`Всего сообщений: ${comments.length}`}
            itemLayout="horizontal"
            renderItem={(props, idx) => {
                let body = {...props};
                body.datetime = moment(props.datetime)
                    .locale("ru")
                    .format("dddd, D MMMM YYYY г., HH:mm");
                if (body.datetime === "Invalid date") {
                    body.datetime = props.datetime;
                }
                body.text = props.content.props.children
                body.key = idx;
                body.content = (
                    <>
                        {body.text.includes("\n") ? (<>
                            <p style={{color: body.color ? body.color : "black"}}>
                                {body.text.split("\n")[0]}
                            </p>
                            <p>
                                {body.text.split("\n")[1]}
                            </p>
                            <p>
                                {body.text.split("\n")[2]}
                            </p>
                            <p>
                                {body.text.split("\n")[3]}
                            </p>
                            <p>
                                {body.text.split("\n")[4]}
                            </p>
                        </>) : <p>{body.text}</p>}
                    </>
                );

                return (
                    <Comment
                        key={body.key}
                        content={body.content}
                        datetime={body.datetime}
                        author={body.author}
                    />
                );
            }}
        />
    );
};
